import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home.vue'
import Nav from '../components/Nav.vue'
import Admission from '../components/Admission.vue'
import Webdesign from '../components/Webdesign.vue'
import About from '../components/About.vue'
import Contact from '../components/Contact.vue'
import Main from '../components/Main.vue'
import Career from '../components/Career.vue'
import Consultancy from '../components/Consultancy.vue'
import Softwareprojects from '../components/Softwareprojects.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/Nav',
    name: 'Nav',
    component: Nav
  },
  {
    path: '/Admission',
    name: 'Admission',
    component: Admission
  },
  {
    path: '/Consultancy',
    name: 'Consultancy',
    component: Consultancy
  },
  {
    path: '/Webdesign',
    name: 'Webdesign',
    component: Webdesign
  },
  {
    path: '/About',
    name: 'About',
    component: About
  },
  {
    path: '/Contact',
    name: 'Contact',
    component: Contact
  },
  
  {
    path: '/Career',
    name: 'Career',
    component: Career
  },
  {
    path: '/',
    name: 'Main',
    component: Main
  },
  {
    path: '/',
    name: 'Softwareprojects',
    component: Softwareprojects
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior(to,from,savedPostion){
    if (to.hash){
      return{
        selector:to.hash,
        behaviour:'smooth',
      };
    }
    if(savedPostion){
      return savedPostion;
    }
    return {x:0, y:0};

  },
  routes
})

export default router
